body {
  margin: 0;
  font-family: 'Gabriola', Georgia, serif;
  font-size: 20px;
  line-height: 1.8;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(to bottom, #E6E6FA, #4B0082); */
  background-color: #E6E6FA;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  width: '100%';
  padding: 10px;
  color: #D8BFD8;
}

h1 {
  text-align: center;
  color: #9966CC;
}

h2 {
  text-align: center;
  color: #9966CC;
}

h3 {
  color: #9966CC;
  text-decoration: underline;
}

h4 {
  color: #9966CC;
}

h5 {
  color: #9966CC;
}

h6 {
  color: #9966CC;
}

nav#nav1 {
  background-color: #7851A9;
  color: white;
  text-align: center;
  padding: 12px;
  font-size: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  border-bottom: 2px solid #DA70D6;
}

a {
  color: #8E4585;
  text-decoration: underline;
  transition: color 0.3s ease;
}

a:hover {
  color: #DA70D6;
}

.link-style {
  color: #FFFFFF;
  padding: 10px;
  text-decoration: none;
}

.link-style.active {
  font-weight: bold;
  color: #DA70D6;
}

p#spacing {
  padding: 20px;
  color: #000000;
}

p#foot {
  color: #8E4585;
  text-align: right;
}

hr {
  color: #DA70D6;
  height: 1.5px;
}

.download-resume {
  display: inline-block;
  color: white;
  background-color: #7851A9;
  text-decoration: none;
  border: 2px solid #DA70D6;
  border-radius: 25px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-align: center;
  height: 40px;
  width: 150px;
  line-height: 40px;
  font-weight: bold;
}

div#float-left {
  float: left;
  width: 30%;
}

div#float-right {
  float: right;
  width: 30%;
}

div#float-right2 {
  float: right;
  width: 30%;
}

table {
  width: 100%;
}

td,
th {
  border: 1px solid #DA70D6;
  padding: 8px;
}